
/* https://www.w3schools.com/howto/howto_js_navbar_shrink_scroll.asp */
#navbar {
    overflow: hidden;
    padding: 30px 10px; /* Large padding which will shrink on scroll (using JS) */
    transition: 0.4s; /* Adds a transition effect when the padding is decreased */
    position: fixed; /* Sticky/fixed navbar */
    width: 100%;
    top: 0; /* At the top */
    z-index: 99;
    background-color: #2c3e50;
}

#tabs{
    font-size: 20px;
    font-weight: bold;
}

#title{
    font-weight: bold;
}

/* is behind navbar so we can have our map container be properly underneath header */
#dummy-header {
    width: 100%;
    height: 105.6px;
}

@media(max-width: 600px){
    #title{
        font-size: 13px;
    }
    #tabs{
        font-size: 13px;
        margin-right: 15px;
    }
}